@mixin responsive($breakpoint) {
    @if $breakpoint == galaxy-fold {
        @media only screen and (max-width: 17.5em) { @content };
    } //end max 280px or 17.5em 
    @if $breakpoint == iphone-5 {
        @media only screen and (max-width: 20.0625em) { @content };
    } //end max 321px or 20.0625em 
    @if $breakpoint == galaxy {
        @media only screen and (max-width: 22.5625em) { @content };
    } //end max 361px or  22.5625em 
    @if $breakpoint == iphone-x {
        @media only screen and (max-width: 23.5em) { @content };
    } //end max 376px or 23.5em  
    @if $breakpoint == pixel-2 {
        @media only screen and (max-width: 25.6875em) { @content };
    } //end max 411px or  25.6875em 
    @if $breakpoint == phone-500 {
        @media only screen and (max-width: 31.25em) { @content };
    } //end max 500px or 31.25em 
    @if $breakpoint == small-phone {
        @media only screen and (max-width: 35.9375em) { @content };
    } //end max 574px or 35.9375em 
    @if $breakpoint == phone {
        @media only screen and (min-width: 35.875em) { @content };
    } // end min 574px or 35.875em  
    @if $breakpoint == tablet-min {
        @media only screen and (min-width: 40.625em) { @content };
    } // end min 650px or 40.625em 
    @if $breakpoint == tablet {
        @media only screen and (min-width: 47.9375em) { @content };
    } // end min 767px or 47.9375em        
    @if $breakpoint == tablet-max {
        @media only screen and (max-width: 48em) { @content };
    } // end max 768px or 48em 
    @if $breakpoint == tablet-plus {
        @media only screen and (min-width: 51.875em) { @content };
    } // end min 830px or 51.875em  
    @if $breakpoint == big-tablet {
        @media only screen and (min-width: 62em) { @content };
    } // end min 992px or 62em   
    @if $breakpoint == laptop {
        @media only screen and (min-width: 64.3125em) { @content };
    } // end min 1029px or 64.3125em 
    @if $breakpoint == laptop-max {
        @media only screen and (max-width: 64.375em) { @content };
    } // end max 1030px  or 64.375em 
    @if $breakpoint == desktop {
        @media only screen and (min-width: 74.9375em) { @content };
    } //end min 1199px or 74.9375em  
    @if $breakpoint == desktop-max {
        @media only screen and (max-width: 75em) { @content };
    } //end max 1200px or 75em 
    @if $breakpoint == big-desktop {
        @media only screen and (min-width: 87.5em) { @content };
    } // end min 1400px or 87.5em 
    @if $breakpoint == developer {
        @media only screen and (min-width: 100em) { @content };
    } // end min 1600px or 100em 
    @if $breakpoint == developer-plus {
        @media only screen and (min-width: 112.5em) { @content };
    } // end min 1800px or 112.5em  
    @if $breakpoint == developer-standard {
        @media only screen and (min-width: 118.75em) { @content };
    } // end min 1920px or 118.75em 
    @if $breakpoint == tv {
        @media only screen and (min-width: 125em) { @content };
    } // end min 2000px or 125em 
    @if $breakpoint == mid-tv {
        @media only screen and (min-width: 140.625em) { @content };
    } // end min 2250px or 140.625em 
    @if $breakpoint == big-tv {
        @media only screen and (min-width: 156.25em) { @content };
    } // end min 2500px or 156.25em 

} //end mixin
