$anomatpeoe:"yes";
$main-color:#fffff0;

.footer {
display:grid;
grid-template-columns:1fr; 
grid-template-rows:auto;                
min-height:150px;
width:100%;
background:$color-footer-background;
align-items:center;

@include text-fluid($font-family-ads, 400, $footer-text-color, 361px, 575px, 14px, 22px);

@include responsive(phone){
@include text-fluid($font-family-ads, 400, $footer-text-color, 575px, 768px, 22px, 28px);
}//end min 574px or 35.875em

@include responsive(tablet){
@include text-fluid($font-family-ads, 400, $footer-text-color, 768px, 992px, 16px, 20px);
}//end min 767px or 47.9375em


@include responsive(big-tablet){
@include text-fluid($font-family-ads, 400, $footer-text-color, 992px, 1200px, 12px, 17px);
}//end min 992px or 62em

@include responsive(desktop){
@include text-fluid($font-family-ads, 400, $footer-text-color, 1200px, 1920px, 10px, 24px);
}//end min 1199px or 74.9375em


@include responsive(developer-standard){
@include text-fluid($font-family-ads, 400, $footer-text-color, 1920px, 2250px, 19px, 26px);
}//end min 1920px or 118.75em

@include responsive(mid-tv){
@include text-fluid($font-family-ads, 400, $footer-text-color, 2250px, 2880px, 12px, 28px);
}//end min 2250px or 140.625em


@include responsive(big-tv){
grid-template-columns:1fr minmax(auto, 2500px) 1fr;
}//end min 2500px or 156.25em
& > .footer_container{
grid-column:1; 
grid-row:1;
display:grid;
grid-template-columns: 1fr 1fr 1fr;
grid-template-rows:.5fr 1fr .5fr;
height:100%;
width:100%;
align-items:center;
padding:$pad-small;

@include responsive(pixel-2){
grid-template-rows:.5fr 1fr .5fr .5fr;
}

@include responsive(big-tv){
grid-column:2; 
}//2500px

text-align:center;
& > .hugo {
grid-column:1/4;
grid-row:1;
height:auto;
width:100%;
margin:5px 0px;
text-align:center;
align-self:start;
font-size:1.2em;
@include responsive(small-phone){margin:1.3rem 0rem;}//575px
@include responsive(phone){margin:1.3rem 0rem;}//575px
}//end hugo
& > .icons {
font-size: $big;
@include transition;
display:flex;
align-items:center;


@include responsive(small-phone){width:35%;}

@include responsive(phone-500){width:33%;}

@include responsive(pixel-2){width:30%;}

@include responsive(galaxy){width:28%;}

@include responsive(iphone-5){width:26%;}

@include responsive(galaxy-fold){width:22%;}

@include responsive(phone){width:35%;}

@include responsive(tablet-min){width:36%;}

@include responsive(tablet){width:38%;}

@include responsive(tablet-plus){width:40%;}

@include responsive(big-tablet){width:40%;}

@include responsive(laptop){width:42%;}

@include responsive(desktop){width:43%;}

@include responsive(developer){width:44%;}

@include responsive(developer-plus){width:45%;}

@include responsive(tv){width:46%;}

@include responsive(tv){width:46%;}

@include responsive(mid-tv){width:46%;}

@include responsive(big-tv){}

& > svg{
fill:#939a9e;

@include responsive(small-phone){
height:2.3rem;
width:2.3rem;
}//500px x 1.15 = 574px

@include responsive(phone){
height:2.2rem;
width:2.2rem;
}//500px x 1.15 = 574px

@include responsive(big-desktop){
height:1.8rem;
width:1.8rem;
}//500px x 1.15 = 574px

}//end .svg
}//end .icons

& > .icons.facebook{

& > svg{
fill:#939a9e;

@include responsive(small-phone){
height:2.2rem;
width:2.2rem;
}//500px x 1.15 = 574px

@include responsive(phone){
height:2.1rem;
width:2.1rem;
}//500px x 1.15 = 574px

@include responsive(big-desktop){
height:1.7rem;
width:1.7rem;
}//500px x 1.15 = 574px

}//end svg
}//end icons.facebook


& > .icons > svg:hover{
transform: scale(1.15);
}
& > a.facebook{
justify-self:start;
grid-column:1/4;
grid-row:2;
display:flex;
align-items:center;
justify-content:end;
margin: 0px 10px 0px 10px;
}//end a.facebook
& > a.develop{
justify-self:center;
grid-column:2;
grid-row:2;
width:100px;
height:50px;

display:flex;
align-items:center;
justify-content:center;
margin: 0px 10px 0px 10px;


@include responsive(phone){
margin: 10px;
height:50px;
width:100px;
}
@include responsive(big-tablet){
height:60px;
width:110px;
margin: 15px 10px 15px 10px;
}
& > .footer-logo {
border-radius:$rad-normal;
height:100%;
width:auto;
transition: all .2s ease-in-out;
align-items:center;
justify-content:center;
display:flex;
& > .img {
width:100%;
height:100%;
object-fit:cover;
object-position:center center;
border-radius:$rad-normal;
}//end image
}//end footer logo picture tag
}//end develop link <a>
& > a.develop:hover{
transform: scale(1.2);
}
& > a.subscribe-link{
justify-self:end;
grid-column:1/4;
grid-row:2;
display:flex;
align-items:center;
justify-content:start;
margin: 0px 10px 0px 10px;
}//end a.subscribe-link
& > section{
display:flex;
flex-wrap:wrap;
align-content:center;
justify-content:center;
}//end sections
& > .footer-right{
grid-column:2;
grid-row:3;
align-items:center;
text-align:center;

@include responsive(phone-500){
grid-column:2;
grid-row:3;
text-align:center;
}
& > .working, & > .email{
height:auto;
width:100%;


& > a{
color:$color-footer-links;
font-size:1em;
text-decoration:underline;
padding: .2em;

@include responsive(small-phone){
padding: .3em;
}//500px x 1.15 = 574px
}//a
}
&  a:hover{
color: $color-footer-links-hover;
font-size:102%;
}//end hover
}//end footer right
& > .terms{
grid-column:1/4;
grid-row:4;
align-items: center;
text-align: center;
padding: 0em 0em;
margin:0;
}

& > .terms a{
font-size:1em;
color:$color-footer-links;
}//a


& > .terms a:hover{
color: $color-footer-links-hover;
font-size:102%;
}//end hover
& > .copy{
grid-column:1/4;
grid-row:5;
margin:8px 0px 10px 0px;

@include responsive(phone){
margin:1em 0em 1em 0em;
}

@include responsive(big-desktop){
margin:1em 0em 1em 0em;
}

}//end .copy
}//end footer_container
}//end footer
